import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Row, Col, Divider, Card } from 'antd';
import { pageLoadedLogin } from '../../../../application/actions/ui';
import FormLaporanSaldoBahan from '../../../component/reportComponent/saldo_bahan/form';
import TableLaporanSaldoBahan from '../../../component/reportComponent/saldo_bahan/tabel';
import Btnprint from '../../../component/reportComponent/saldo_bahan/btnprint';

const LaporanSaldoBahan = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(pageLoadedLogin);
    document.title = 'Laporan Saldo Bahan';
  }, [dispatch]);

  return (
    <Card title="Laporan Saldo Bahan" bordered={false}>
      <Row>
        <Col span={24}>
          <FormLaporanSaldoBahan />
        </Col>
        <Col
          span={24}
          // style={{ marginTop: 10, display: datalapR.length === 0 ? 'none' : '' }}
        >
          <Divider orientation="left">Tabel Laporan</Divider>
          <TableLaporanSaldoBahan />
        </Col>
        <Col
          span={24}
          // style={{ marginTop: 10, display: datalapR.length === 0 ? 'none' : '' }}
        >
          <Btnprint />
        </Col>
      </Row>
    </Card>
  );
};

export default LaporanSaldoBahan;
