import React, { Component } from 'react';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';

class ExcelReport extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="ant-btn ant-btn-primary ant-btn-block ant-btn-success"
          table="table-to-xls"
          filename="LAPORAN SALDO BAHAN"
          sheet="LAPORAN SALDO BAHAN"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: 'none' }}>
          <thead>
            <tr>
              <th colSpan="14" style={{ textAlign: 'center' }}>
                {' '}
                LAPORAN SALDO BAHAN{' '}
              </th>
            </tr>
            <tr>
              <th colSpan="14"> Tanggal : </th>
            </tr>
            <tr>
              <td
                style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}
                colSpan="12"
              >
                SALDO BAHAN
              </td>
              <td
                style={{
                  backgroundColor: '#99CCFF',
                  color: '#000',
                  textAlign: 'center',
                }}
                colSpan="2"
              >
                BAHAN CAMPURAN
              </td>
            </tr>
            <tr>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                JENIS
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                BARU
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                KARAT
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                LAMA
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                KARAT
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                KALI
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                SPRU
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                KARAT
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                KALI
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                JEND. CASTING
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                TOTAL
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                KARAT
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                BERAT
              </td>
              <td style={{ backgroundColor: '#99CCFF', color: '#000', textAlign: 'center' }}>
                KARAT
              </td>
            </tr>
          </thead>
        </table>
      </>
    );
  }
}
export default ExcelReport;
